<template>
  <div>
    <el-card class="box-card">
      <div class="search" style="text-align:left;margin-bottom:20px;">
        <span>名称：</span>
        <el-input placeholder="搜索素材" v-model="dataForm.imageName" class="input-with-select searchInput">
          <el-button @click="getData" slot="append" icon="el-icon-search"></el-button>
        </el-input>
      </div>
      <el-card class="box-card">
        <div v-if="list.length > 0">
          <div style="line-height:24px;"><el-checkbox @change="allSelect" v-model="allChecked">全选</el-checkbox></div>
          <div class="classList" :style="{height: boxHeight + 'px'}">
            <ul class="infinite-list flex-row-start1 ul" style="overflow-x: hidden;overflow-y:auto;height:100%;">
              <li v-for="(item,index) in list" :key="index" class="infinite-list-item item">
                <div class="demo-image__preview">
                  <el-image class="curImg" v-if="item.type == 1" @click="showBigImg(item.imageUrl)" style="width: 200px; height: 200px" fit="fill" :src="item.imageUrl" :preview-src-list="srcList"></el-image>
                  <el-image class="curImg" v-if="item.type == 2" @click="playVideo(item.imageUrl)" style="width: 200px; height: 200px" fit="fill" :src="item.coversUrl"></el-image>
                  <div class="imgNameBox">
                    <el-checkbox @change="checkChange(item)" v-model="item.checked">{{item.imageName}}</el-checkbox>
                  </div>
                  <div style="text-align:center;">
                    <el-button type="text" @click="changeShow(item)">恢复</el-button>
                  </div>
                </div>
              </li>
            </ul>
          </div>
          <div class="fy flex-row-between">
            <div>
              <el-button type="primary" @click="allChangeBack">批量恢复</el-button>
            </div>
            <div>
              <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :page-sizes="[10, 20, 30]"
                :page-size="dataForm.pageSize"
                layout="total, sizes, prev, pager, next, jumper"
                :total="listTotal">
              </el-pagination>
            </div>
          </div>
        </div>
        <div v-else>暂无数据</div>
      </el-card>
    </el-card>
    <!-- 图片分组 -->
    <el-dialog title="恢复图片分组" :visible.sync="changeClassShow" width="30%">
      <div>
        <el-select @change="change($event)" v-model="changeClassForm.id" placeholder="请选择分组">
          <el-option v-for="(item,index) in backImgClassArr" :key="index" :label="item.galleryName" :value="item.id"></el-option>
        </el-select>
      </div>
      <span slot="footer" class="dialog-footer"> 
        <el-button @click="changeClassShow = false">取 消</el-button>
        <el-button :loading="changeClassLoading" type="primary" @click="subChangeClass">确 定</el-button>
      </span>
    </el-dialog>
    <!-- 视频分组 -->
    <el-dialog title="恢复视频分组" :visible.sync="changeVideoClassShow" width="30%">
      <div>
        <el-select @change="change($event)" v-model="changeVideoClassForm.id" placeholder="请选择分组">
          <el-option v-for="(item,index) in backVadioClassArr" :key="index" :label="item.galleryName" :value="item.id"></el-option>
        </el-select>
      </div>
      <span slot="footer" class="dialog-footer"> 
        <el-button @click="changeVideoClassShow = false">取 消</el-button>
        <el-button :loading="changeVideoClassLoading" type="primary" @click="subVideoChangeClass">确 定</el-button>
      </span>
    </el-dialog>
    <!-- 播放视频 -->
    <el-dialog title="" :visible.sync="videoPlayShow" width="30%">
      <video
        :src="videoPlay.imageUrl"
        style="width:100%;"
        class="avatar video-avatar"
        controls="controls">
        您的浏览器不支持视频播放
      </video>
    </el-dialog>
  </div>
</template>
<script>
import { mapState } from 'vuex'
import { 
  getDeleteGallery,
  updateDeleteStatus,
  getGalleryByShopId
} from '@/api/shop/shopMaterial'
export default {
  props: ["getPost"],
  watch:{
    getPost(newvalue,oldValue){
      if(newvalue){
        this.getData()
        this.getGaller(1)
        this.getGaller(2)
      } 
    }
  },
  data(){
    return{
      dataForm:{
        pageNum:1,
        pageSize:30,
        imageName:'',
      },
      list:[],
      listTotal:0,
      allChecked:false,
      boxHeight:0,
      srcList:[],
      selectImgs:[],

      backImgClassArr:[],                 // 图片分组数据
      changeClassShow:false,
      changeClassForm:{},                 // 批量分组的数据
      changeClassLoading:false,           // 批量分组loading
      
      
      backVadioClassArr:[],               // 影音分组数据
      changeVideoClassShow:false,
      changeVideoClassForm:{},
      changeVideoClassLoading:false,      

      videoPlayShow:false,
      videoPlay:{},
    }
  },
  mounted() {
    this.$nextTick(function() {
      this.boxHeight = window.innerHeight - 354;
    })
    window.onresize = () => {
      return (() => {
        this.boxHeight = window.innerHeight - 354;
      })();
    };
  },
  methods:{
    // 获取数据
    getData(){
      console.log('执行了')
      this.dataForm.shopId = this.shop.shopId
      getDeleteGallery(this.dataForm).then(res=>{
        console.log('回收站数据',res)
        this.list = res.data.list
        this.listTotal = res.data.total
      })
    },
    // 分类 - 获取图片库分类
    getGaller(e){
      let obj = {};
      obj.type = e;
      obj.shopId = this.shop.shopId
      console.log('请求分类的数据',obj)
      getGalleryByShopId(obj).then(res=>{
        console.log(e==1?'获取图片库分组':'获取视频分组',res)
        let arr = []
        for(var i=1;i<res.data.length;i++){
          arr.push(res.data[i])
        }
        if(e == 1) this.backImgClassArr = arr; this.changeClassForm.id = arr[0].id
        if(e == 2) this.backVadioClassArr = arr; this.changeVideoClassForm.id = arr[0].id
      })
    },
    // 全选
    allSelect(e){
      if(e){
        this.list.forEach(e=>{
          e.checked = true
          this.selectImgs.push(e)
        })
      }
      if(!e){
        this.list.forEach(e=>{e.checked = false})
        this.selectImgs = []
      }
    },
    // 单选
    checkChange(e){
      if(e.checked){
        this.selectImgs.push(e)
      }else{
        console.log('2')
        this.selectImgs.splice(this.selectImgs.findIndex(item => item.id === e.id), 1)
      }
      this.allChecked = this.selectImgs.length == this.listTotal
    },
    // 搜索获取焦点
    // 恢复分组弹出框
    changeShow(e){
      if(e.type == 1){
        this.changeClassForm.ids = e.id;
        this.changeClassShow = true;
      }
      if(e.type == 2){
        this.changeVideoClassForm.ids = e.id;
        this.changeVideoClassShow = true;
      }
    },
    // 批量恢复
    allChangeBack(){
      console.log(this.selectImgs)
      if(this.selectImgs.length == 0){
        this.$message.error('请勾选后重试');
        return;
      }
      let arr = Array.from(new Set(this.selectImgs.map(item => item.type)))
      if(arr.length > 1){
        this.$message.error('无法同时恢复图片和视频');
        return;
      }
      console.log(arr)
      let arr1 = []
      this.selectImgs.forEach(e=>arr1.push(e.id))
      if(arr[0] == 1){
        this.changeClassForm.ids = arr1.join(',')
        this.changeClassShow = true;
      }
      if(arr[0] == 2){
        this.changeVideoClassForm.ids = arr1.join(',')
        this.changeVideoClassShow = true;
      }
    },
    // 恢复操作
    subChangeClass(){
      this.changeClassLoading = true;
      let obj = {};
      obj.id = this.changeClassForm.ids;
      obj.galleryId = this.changeClassForm.id;
      updateDeleteStatus(obj).then(res=>{
        console.log('恢复分组',res)
        if(res.code == 200){
          this.$message({
            message: '恢复成功',
            type: 'success'
          });
          this.getData()
          this.selectImgs = []
          this.allSelect = false;
          this.changeClassShow = false;
          setTimeout(()=>{
            this.changeClassLoading = false;
          },1000)
        }else{
          this.changeClassLoading = false;
          this.$message.error(res.message);
        }
      })
    },
    // 视频 - 提交分组
    subVideoChangeClass(){
      this.changeVideoClassLoading = true;
      let obj = {};
      obj.id = this.changeVideoClassForm.ids;
      obj.galleryId = this.changeVideoClassForm.id;
      updateDeleteStatus(obj).then(res=>{
        console.log('恢复分组',res)
        if(res.code == 200){
          this.$message({
            message: '分组成功',
            type: 'success'
          });
          this.getData()
          this.selectImgs = []
          this.changeVideoClassShow = false;
          setTimeout(()=>{
            this.changeVideoClassLoading = false;
          },1000)
        }else{
          this.changeVideoClassLoading = false;
          this.$message.error(res.message);
        }
      })
    },

    // 默认
    // 图片 - 点击图片放大
    showBigImg(e){
      this.srcList = [];
      this.srcList.push(e)
    },
    // 图片 - 点击图片放大
    playVideo(e){
      this.videoPlayShow = true;
      this.videoPlay.imageUrl = e
    },
    // 图片 - 分页
    handleSizeChange(val) {
      this.dataForm.pageSize = val;
      this.getData()
    },
    // 图片 - 分页
    handleCurrentChange(val) {
      this.dataForm.pageNum = val;
      this.getData()
    },
    // 文本框输入
    change(e){
      this.$forceUpdate()
    }
  },
  computed: {
    ...mapState([
      'shop'
    ])
  },
}
</script>
<style lang="less" scoped>
  .curImg{
    cursor: pointer;
  }
  .search{
    position: relative;
    text-align: center;
    .searchInput{
      width: 200px;
    }
  }
  .ul{
    flex-wrap: wrap;
  }
  .item{
    margin: 20px 20px 0 0 ;
    width: 200px;
    .imgNameBox{
      width: 100%;
      text-align: left;
      span{
        margin-left: 4px;
        width: 180px;
        font-size: 12px;
        color: #409EFF;
        display:-webkit-box;
        -webkit-box-orient:vertical;
        -webkit-line-clamp:1;/*我设置的是文字超过1行显示...；行数可以随便改；*/
        overflow:hidden;
        cursor: pointer;
      }
    }
  }
</style>
<style scoped>
  .classList /deep/ .el-scrollbar__bar.is-horizontal{
    display: none;
  }
  .el-checkbox /deep/ .el-checkbox__label {
    width: 186px;
    white-space:nowrap;
    overflow:hidden;
    text-overflow:ellipsis;
    vertical-align: top;
  }
</style>