<template>
  <div class="noTopContent">
    <el-tabs ref="tabs" v-model="activeName" @tab-click="changePage">
      <el-tab-pane label="图片" name="image">
        <imageMaterial :getPost="getImg"></imageMaterial>
      </el-tab-pane>
      <el-tab-pane label="视频" name="video">
        <videoMaterial :getPost="getVideo"></videoMaterial>
      </el-tab-pane>
      <el-tab-pane label="回收站" name="recovery">
        <recoveryMaterial :getPost="getRecovery"></recoveryMaterial>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>
<script>
import imageMaterial from './components/imageMaterial';
import videoMaterial from './components/videoMaterial';
import recoveryMaterial from './components/recoveryMaterial';
export default {
  data(){
    return{
      activeName:'image',           // 默认分页展示
      getImg:false,
      getVideo:false,
      getRecovery:false,
    }
  },
  methods:{
    // 点击nav
    changePage(tab, event) {
      console.log(tab, event);
      if(tab.index == 0){
        this.getImg = true;
        this.getVideo = false;
        this.getRecovery = false;
      }
      if(tab.index == 1){
        this.getImg = false;
        this.getVideo = true;
        this.getRecovery = false;
      }
      if(tab.index == 2){
        this.getImg = false;
        this.getVideo = false;
        this.getRecovery = true;
      }
    },
  },
  components: {
    imageMaterial,
    videoMaterial,
    recoveryMaterial
  },
}
</script>
<style lang="less" scoped>
.noTopContent{
  padding: 0 20px 20px;
}
</style>